___CSS_LOADER_EXPORT___.push([module.id, "\n.container[data-v-2bbc185a] {\n\twidth: 100%;\n\tmax-width: 1200px;\n\tmargin: 0 auto;\n\tpadding: 20px 10px;\n}\nh1[data-v-2bbc185a] {\n\ttext-align: center;\n\tmargin: 10px auto;\n\tfont-size: 24px;\n\tfont-weight: bold;\n}\n", "",{"version":3,"sources":["webpack://./src/views/Home.vue"],"names":[],"mappings":";AAwFA;CACA,WAAA;CACA,iBAAA;CACA,cAAA;CACA,kBAAA;AACA;AAEA;CACA,kBAAA;CACA,iBAAA;CACA,eAAA;CACA,iBAAA;AACA","sourcesContent":["<!--\n - @copyright Copyright (c) 2022 Andrey Borysenko <andrey18106x@gmail.com>\n -\n - @copyright Copyright (c) 2022 Alexander Piskun <bigcat88@icloud.com>\n -\n - @author 2022 Andrey Borysenko <andrey18106x@gmail.com>\n -\n - @license AGPL-3.0-or-later\n -\n - This program is free software: you can redistribute it and/or modify\n - it under the terms of the GNU Affero General Public License as\n - published by the Free Software Foundation, either version 3 of the\n - License, or (at your option) any later version.\n -\n - This program is distributed in the hope that it will be useful,\n - but WITHOUT ANY WARRANTY; without even the implied warranty of\n - MERCHANTABILITY or FITNESS FOR A PARTICULAR PURPOSE. See the\n - GNU Affero General Public License for more details.\n -\n - You should have received a copy of the GNU Affero General Public License\n - along with this program. If not, see <http://www.gnu.org/licenses/>.\n -\n -->\n\n<template>\n\t<div class=\"container\" style=\"text-align: center;\">\n\t\t<h1>{{ rootTitle }}</h1>\n\t\t<div class=\"python-test\">\n\t\t\t<NcButton :disabled=\"requesting\"\n\t\t\t\tstyle=\"margin: 10px auto;\"\n\t\t\t\t@click=\"requestPythonRun\">\n\t\t\t\t<template #default>\n\t\t\t\t\t{{ t('watermark', 'Request Python check') }}\n\t\t\t\t</template>\n\t\t\t\t<template v-if=\"requesting\" #icon>\n\t\t\t\t\t<span class=\"icon-loading-small\" />\n\t\t\t\t</template>\n\t\t\t</NcButton>\n\t\t\t<div v-if=\"pythonOutput !== null\" class=\"python-output\">\n\t\t\t\t<pre>{{ pythonOutput }}</pre>\n\t\t\t</div>\n\t\t</div>\n\t</div>\n</template>\n\n<script>\nimport NcButton from '@nextcloud/vue/dist/Components/NcButton.js'\n\nimport axios from '@nextcloud/axios'\nimport { generateUrl } from '@nextcloud/router'\nimport { showError } from '@nextcloud/dialogs'\n\nexport default {\n\tname: 'Home',\n\tcomponents: {\n\t\tNcButton,\n\t},\n\tprops: {\n\t\trootTitle: {\n\t\t\ttype: String,\n\t\t\trequired: true,\n\t\t},\n\t},\n\tdata() {\n\t\treturn {\n\t\t\trequesting: false,\n\t\t\tpythonOutput: null,\n\t\t}\n\t},\n\tbeforeMount() {\n\t\tthis.$emit('update:loading', false)\n\t},\n\tmethods: {\n\t\trequestPythonRun() {\n\t\t\tthis.requesting = true\n\t\t\taxios.get(generateUrl('/apps/watermark/api/v1/python/check')).then(res => {\n\t\t\t\tthis.pythonOutput = res.data\n\t\t\t\tthis.requesting = false\n\t\t\t}).catch(err => {\n\t\t\t\tconsole.debug(err)\n\t\t\t\tshowError(this.t('watermark', 'A server error occurred'))\n\t\t\t\tthis.requesting = false\n\t\t\t})\n\t\t},\n\t},\n}\n</script>\n<style scoped>\n.container {\n\twidth: 100%;\n\tmax-width: 1200px;\n\tmargin: 0 auto;\n\tpadding: 20px 10px;\n}\n\nh1 {\n\ttext-align: center;\n\tmargin: 10px auto;\n\tfont-size: 24px;\n\tfont-weight: bold;\n}\n</style>\n"],"sourceRoot":""}]);
